<template>
  <div class="tripmap">
    <!-- <div class="container-fluid content"> -->
      <!-- <h4>建立旅程</h4> -->
      <div class="map">
        <GoogleMapLoader
          ref="googlemaploader"
          :map-config="mapConfig"
          :api-key="apiKey"
        />
      </div>
      <div class="trip" v-for="p in pagename">
        <MapTrip v-if="p === 'mapaddschedule'" :trip="trip" msg="Welcome to Your Vue.js App" />
        <MapHotspot msg="Welcome to Your Vue.js App" />
        <MapSearchPlace v-if="p === 'mapsearchplace'" v-on:placePosition="placePosition" msg="Welcome to Your Vue.js App" />
        <MapPlaceDetail v-if="p === 'mapplacedetail'" msg="Welcome to Your Vue.js App" />
      </div>
    </div>
</template>

<script>
// @ is an alias to /src
import store from "@/store";
import GoogleMapLoader from '@/components/GoogleMapLoader'
import MapTrip from './components/MapTrip.vue'
import MapHotspot from './components/MapHotspot.vue'
import MapPlaceDetail from './components/MapPlaceDetail.vue'
import MapSearchPlace from './components/MapSearchPlace.vue'

export default {
  components: {
    GoogleMapLoader,
    MapTrip,
    MapHotspot,
    MapPlaceDetail,
    MapSearchPlace
  },
  mounted() {
    this.getTrip()
  },
  data() {
    return {
      markers: [],
      apiKey: process.env.VUE_APP_GOOGLEMAPAPIKEY,
      trip: []
    }
  },
  computed: {
    mapConfig() {
      return {
        zoom: 8,
        center: this.mapCenter
      }
    },
    mapCenter() {
      return { lng: 120.93891, lat: 23.79278 }
    },
    pagename() {
      return store.getters.pagename
    },
  },
  methods: {
    getTrip(){
        this.trip = this.$route.params.data
    },
    placePosition(){
      this.$refs.googlemaploader.searchMarkerPosition();
    },
  }
}
</script>

<style>
.tripmap {
  position: relative;
}
.google-map {
  position: absolute;
  width: 100%;
  height: 87.4vh;
  z-index: 1;
}
.trip {
  position: absolute;
  display: grid;
  grid-template-columns: 26vw 26vw 23.8vw 24.2vw;
}
</style>
