<template>
  <div class="map-search-place">
    <div class="row">
      <div class="col-md-12 justify-content-center nicefont">
        <!-- <div class="card justify-content-center"> -->
        <div class="map-search-place-card card">
          <div class="card-top">
            <div class="d-flex flex-wrap">
              <div class="map-search-place-nav col-12 fz-1-0 left">
                <ul>
                  <li><a @click="switchPage('mapaddschedule')">返回</a></li>
                  <li><a href="">搜尋</a></li>
                  <li><a href="">收藏</a></li>
                  <li><a href="">自訂</a></li>
                </ul>
              </div>
              <div class="map-search-place-search col-5">
                <div class="fz-1-0 left">
                  <input v-model="keyword" title="title" type="text" autocomplete="off" placeholder="輸入關鍵字">
                </div>
              </div>
              <div class="map-search-place-search-btn col-3">
                <button @click="mapSearchPlace()" type="button">
                  搜尋
                </button>
              </div>
            </div>
          </div>
          <div class="map-search-place-search-result padding-0-1 overflow-auto">
            <div @click="placeDetail(place);placePosition()" class="place-result-0 margin-0-5" v-bind:key="place.json_d" v-for="place in results">
              <div class="d-flex flex-wrap align-items-center place-result-contain">
                <div class="col-8">
                  <div class="col-12 fw-bold fz-1-0 padding-0-1 left">{{ place.name }}</div>
                  <div class="col-12 fz-0-5 padding-0-1 left">{{ place.add }}</div>
                </div>
                <div class="col-4">
                  <img class="place-result-img" :src="place.picture1" alt="Image">
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getSearchPlace } from '@/api/place'
import store from '@/store'

export default {
  name: 'MapSearchPlace',
  props: {
    msg: String
  },
  data() {
    return {
      keyword: this.value,
      results: []
    }
  },
  computed: {
    schedule_list() {
      return store.getters.schedule_list
    }
  },
  methods: {
    placeDetail(place){
      store.commit('gotravel/placeName', place.name)
      store.commit('gotravel/placePicture', place.picture1)
      store.commit('gotravel/placePx', place.px)
      store.commit('gotravel/placePy', place.py)
      this.showPage('mapplacedetail')
    },
    switchPage(pagename) {
      store.commit('gotravel/switchPage', pagename)
    },
    showPage(pagename) {
      store.commit('gotravel/showPage', pagename)
    },
    mapSearchPlace() {
      return new Promise((resolve, reject) => {
        getSearchPlace(this.$data.keyword)
          .then((response) => {
            this.results = response
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    placePosition() {
      this.$emit('placePosition')
    },
    createSchedule() {
      let data = {
        "schedule": [

        ],
        "title": '',
        "description": 'yayaya',
        "url": '',
        "category": '',
        "subcategory": '',
        "author": '',
        "user": {'username':store.getters.uid}
      }
      for (let list of store.getters.schedule_list) {
          let schedule =
                {
            "title": {
              "json_d": ''
            },
            "date": '2021-03-02',
            "day": 1,
            "from_time": '',
            "to_time": '',
          }
          schedule.title.json_d = list.id
          schedule.from_time = schedule.date+' '+list.from
          schedule.to_time = schedule.date+' '+list.to
          data.schedule.push(schedule)
      }
      const Swal = this.$swal
      Swal.fire({
        input: 'text',
        inputLabel: '建立旅程',
        inputPlaceholder: '請輸入旅程名稱',
        inputAttributes: {
          'aria-label': '請輸入旅程名稱'
        },
        showCancelButton: true
      }).then((result)=>{
        if(result.value){
          data.title = result.value
          // Actions support the same payload format and object-style dispatch:
          this.$store.dispatch('schedule/createSchedule', data)
          if (result.value) {
            Swal.fire({
              icon: 'success',
              title: '已建立旅程',
              showConfirmButton: false,
              timer: 1500
            })
          }
        }
      })
    }
  },
  store
}
</script>

