<template>
  <div class="map-place-detail">
    <div class="d-flex flex-wrap">
      <div class="col-md-12 justify-content-center nicefont">
        <!-- <div class="card justify-content-center"> -->
        <div class="map-place-card card">
          <div class="card-top">
            <div class="d-flex flex-wrap">
              <div class="add-place fz-1-5 center col-12">新增景點</div>
              <div class="col-12">
                <div class="fz-2-0 left">{{ placename }}</div>
              </div>
              <div class="col-8">
                <img class="place-img margin-0-1" :src="placepicture" alt="Image">
              </div>
              <div class="place-more-img fz-1-0 col-3">
                <p>更多照片</p>
                <p>來自 Google Maps</p>
              </div>
            </div>
          </div>
          <div class="card-place-open padding-0-1 overflow-auto">
            <div class="place-open fz-1-0 col-12 left">
              <div>開張時段</div>
              <div>星期一:11:00 - 20:00</div>
              <div>星期二:11:00 - 20:00</div>
              <div>星期三:11:00 - 20:00</div>
              <div>星期四:11:00 - 20:00</div>
              <div>星期五:11:00 - 20:00</div>
              <div>星期六:11:00 - 20:00</div>
              <div>星期日:11:00 - 20:00</div>
              <div>電話: +886916950028</div>
              <div>地址: 台北市松山區八德路四段</div>
              <div>網站: </div>
              <div>https://ttime-demo/ivankaoblog.com.tw/</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import store from '@/store'

export default {
  name: 'MapPlaceDetail',
  props: {
    msg: String
  },
  computed: {
    placename() {
      return store.getters.placename
    },
    placepicture() {
      return store.getters.placepicture
    }
  },
  methods: {
    createSchedule() {
      let data = {
        "schedule": [
          
        ],
        "title": '',
        "description": 'yayaya',
        "url": '',
        "category": '',
        "subcategory": '',
        "author": '',
        "user": {'username':store.getters.uid}
      }
      for (let list of store.getters.schedule_list) {
          let schedule =
                {
            "title": {
              "json_d": ''
            },
            "date": '2021-03-02',
            "day": 1,
            "from_time": '',
            "to_time": '',
          }
          schedule.title.json_d = list.id
          schedule.from_time = schedule.date+' '+list.from
          schedule.to_time = schedule.date+' '+list.to
          data.schedule.push(schedule)
      }
      const Swal = this.$swal
      Swal.fire({
        input: 'text',
        inputLabel: '建立旅程',
        inputPlaceholder: '請輸入旅程名稱',
        inputAttributes: {
          'aria-label': '請輸入旅程名稱'
        },
        showCancelButton: true
      }).then((result)=>{
        if(result.value){
          data.title = result.value
          // Actions support the same payload format and object-style dispatch:
          this.$store.dispatch('schedule/createSchedule', data)
          if (result.value) {
            Swal.fire({
              icon: 'success',
              title: '已建立旅程',
              showConfirmButton: false,
              timer: 1500
            })
          }
        }
      })
    }
  },
  store
}
</script>

